.scroll-to-top-btn {
	position: fixed;
	bottom: map-get($space, l2);
	#{$start}: map-get($space, l1);
	@include rwd-df(xs) {
		bottom: map-get($space, l4);
		#{$start}: map-get($space, s1);
	}
	z-index: 1001;
	padding: map-get($space, s1);
	background-color: map-get($color-primary, main);
	color: $color-white;
	@include squareSize(map-get($space, l3));
	font-size: map-get($font-size, l2);
	border-radius: 50%;

	font-weight: map-get($font-weight, l3);
	line-height: 0;
	display: flex;
	justify-content: center;
	align-items: center;

	display: block;
	visibility: hidden;
	opacity: 0;
	pointer-events: none;
	transform: scale(0);
	transition: all map-get($time, l1);
	border: map-get($space, s6) solid $color-white;
	@include boxShadow(l3);

	&.show {
		visibility: visible;
		opacity: 1;
		transform: scale(1);
		pointer-events: auto;

		&:hover {
			transform: translateY(-0.3rem);
			box-shadow: rgba(0, 0, 0, 0.2) 0px 1rem 2rem;
			background-color: $color-white;
			border-color: map-get($color-primary, main);
			color: map-get($color-primary, main);
		}
	}
}
