.slider {
	background-color: lightgrey;
	position: relative;
	height: 100%;
	overflow-x: hidden;
	&__slides {
		position: relative;
		height: 100%;

		&__slide {
			display: none;
			justify-content: center;
			align-items: center;
			font-size: map-get($font-size, l6);
			font-weight: map-get($font-weight, l3);

			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			transition: transform map-get($time, s1);
			@include backgroundImage;

			&__content {
				text-align: center;
				color: $color-white;
			}
			&__title {
				font-size: map-get($font-size, l7);
				font-weight: bold;
				line-height: 1.18;
				letter-spacing: normal;
				color: map-get($color-primary, d1);
				text-transform: uppercase;
				// text-shadow: 0 0 3px map-get($color-grey, l7),
				// 	1 0 3px map-get($color-grey, l7), 0 1 3px map-get($color-grey, l7),
				// 	1 1 3px map-get($color-grey, l7);
				text-shadow: 2px 2px 1px #fff, 1px 1px 1px map-get($color-grey, l7);
			}
			&__description {
				font-size: map-get($font-size, l3);
				color: map-get($color-grey, l7);
				font-weight: normal;
				line-height: 1.63;
				margin: map-get($space, l3) map-get($space, l2);

				@include rwd-df(xs) {
					margin: map-get($space, l2) map-get($space, l1) map-get($space, l1);
				}

				@include rwd-only(xxs) {
					margin: map-get($space, l2) map-get($space, main)
						map-get($space, main);
				}

				&__sub {
					@include rwd-df(xs) {
						display: none;
					}
				}
			}

			&:nth-child(1) {
				// background-color: lightpink;

				background-image: linear-gradient(
						to bottom,
						rgba($color-black, 0.65),
						rgba($color-black, 0.65)
					),
					url(../../assets/img/hero/seafood-1.jpg);
			}
			&:nth-child(2) {
				// background-color: lightseagreen;
				background-image: linear-gradient(
						to bottom,
						rgba($color-black, 0.65),
						rgba($color-black, 0.65)
					),
					url(../../assets/img/hero/seafood-2.jpg);
			}
			&:nth-child(3) {
				// background-color: lightskyblue;
				background-image: linear-gradient(
						to bottom,
						rgba($color-black, 0.65),
						rgba($color-black, 0.65)
					),
					url(../../assets/img/hero/seafood-3.jpg);
			}
			&:nth-child(4) {
				// background-color: lemonchiffon;
				background-image: linear-gradient(
						to bottom,
						rgba($color-black, 0.65),
						rgba($color-black, 0.65)
					),
					url(../../assets/img/hero/seafood-4.jpg);
			}
			&:nth-child(5) {
				// background-color: blueviolet;
				background-image: linear-gradient(
						to bottom,
						rgba($color-black, 0.65),
						rgba($color-black, 0.65)
					),
					url(../../assets/img/hero/seafood-5.jpg);
			}
			// &:nth-child(6) {
			// 	background-color: orangered;
			// }
			// &:nth-child(7) {
			// 	background-color: turquoise;
			// }
			// &:nth-child(8) {
			// 	background-color: peru;
			// }

			&--prev,
			&--curr,
			&--next {
				display: flex;
			}

			&--prev {
				transform: translateX(-100%);
			}

			&--curr {
				transform: translateX(0%);
			}

			&--next {
				transform: translateX(100%);
			}
		}
	}
	&__arrow {
		font-size: map-get($font-size, l2);
		font-weight: map-get($font-weight, l2);
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		border-radius: 0.5rem;

		display: flex;
		align-items: center;
		justify-content: center;
		border: map-get($space, s7) solid map-get($color-primary, main);
		background-color: map-get($color-primary, main);
		color: $color-white;
		transition: all map-get($time, main);

		&:hover {
			border-color: $color-white;
			background-color: $color-white;
			color: map-get($color-primary, main);
		}

		@include squareSize(4rem);

		@include rwd-df(xs) {
			display: none;
		}

		&--next {
			right: 2rem;
		}
		&--prev {
			left: 2rem;
		}
	}
}
