/*
**  define utility classes to make clearfix for specified floated elements
*/
.u-clearfix::after {
	content: '';
	display: block;
	clear: both !important ;
}

/*
  **  define utility classes to hidden specified elements
  */
.u-hidden {
	display: none !important;
}

/*
**  define utility class for center inline & inline-block specified elements
*/
.u-center-text {
	text-align: center !important ;
}

/*
**  define utility class for center block element specified elements
*/
.u-absCenter {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

/*
  **  define utility class for add overlayer specified elements
  */
.u-overlayer::after {
	content: '';
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba($color-black, 0.7);
	z-index: 2;
}
