.social-list {
	display: flex;
	gap: map-get($space, main);
	&__item {
		&__link {
			display: block;
			@include squareSize(map-get($space, l2));
			// padding: map-get($space, s7);
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: $color-white;
			border: map-get($space, s7) solid transparent;
			border-radius: 50%;
			transition: all map-get($time, l1);

			color: map-get($color-primary, main);

			&:link,
			&:visited {
			}
			&:hover,
			&:active {
				background-color: transparent;
				color: $color-white;
				border-color: $color-white;
			}

			&__icon {
				@include squareSize(map-get($space, main));
				color: inherit;
			}
		}
	}
}
