.sec-dishes {
	background-image: linear-gradient(
			rgba(27, 22, 22, 0.85),
			rgba(27, 22, 22, 0.85)
		),
		url('../../assets/img/dishes/seafood-bg.jpg');
	@include backgroundImage;
	&__content {
		// text-align: center;
		&__cards {
			display: grid;
			grid-template-columns: repeat(3, 1fr);
			gap: map-get($space, l5);

			margin-bottom: map-get($space, l3);

			@include rwd-df(lg) {
				gap: map-get($space, l3);
			}

			@include rwd-df(md) {
				grid-template-columns: repeat(2, 1fr);
			}

			@include rwd-df(xs) {
				grid-template-columns: repeat(1, 1fr);
				justify-items: center;

				.dish-card {
					width: 85%;
				}
			}
		}
		.btn__container {
			text-align: center;
		}
	}
}
