::-webkit-scrollbar {
	width: map-get($space, s1); /* Mostly for vertical scrollbars */
	// height: 10px; /* Mostly for horizontal scrollbars */
}

::-webkit-scrollbar-track {
	/* Foreground */

	background-color: map-get($color-grey, l7);
	// box-shadow: 0 0 10px #ddd inset;
	@include boxShadow(inner);
	// border: 2px solid map-get($color-primary, main);
}

::-webkit-scrollbar-thumb {
	/* Background */
	margin: 0 map-get($space, s4);
	background-color: map-get($color-primary, l1);
	border-radius: map-get($space, s1);
	transition: all map-get($time, main);
}

::-webkit-scrollbar-thumb:hover {
	background-color: map-get($color-primary, main);
}

::-webkit-scrollbar-corner {
	background-color: map-get($color-primary, l1);
}
