.sec-contact {
	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: map-get($space, l4);
		align-items: start;

		@include rwd-df(md) {
			grid-template-columns: 1fr;
			gap: map-get($space, l3);
		}
	}

	&__map {
		width: 100%;
		height: 30rem;
		border: map-get($space, s7) solid map-get($color-primary, l1);
		padding: map-get($space, s7);
		border-radius: map-get($space, s6);
	}
}
