.btn {
	&,
	&:link,
	&:visited {
		display: inline-block;
		width: fit-content;
		border-radius: map-get($space, s3);
		background-color: $color-white;
		overflow: hidden;
		padding: map-get($space, s3) map-get($space, l1);
		font-size: map-get($font-size, l2);
		text-transform: uppercase;
		color: $color-white;
		letter-spacing: 2px;
		font-weight: map-get($font-weight, l2);
		box-shadow: 0 15px 40px -10px rgba(0, 0, 0, 0.3);
		position: relative;
		transition: all map-get($time, main) ease;

		&:before,
		&:after {
			content: '';
			position: absolute;
			width: 51%;
			height: 100%;
			top: 0;
			z-index: 1;
			transition: all map-get($time, main) ease;
			background-color: map-get($color-primary, main);
		}

		&:before {
			#{$start}: 0;
		}

		&:after {
			#{$end}: 0;
		}
	}

	&__text {
		position: relative;
		z-index: 2;
	}

	&:hover,
	&:active {
		transition: all map-get($time, main) ease;
		box-shadow: 0 8px 20px -12px rgba(0, 0, 0, 0.2);
		color: map-get($color-primary, main);

		&:before,
		&:after {
			width: 4px;
		}
	}
}
