.sec-about {
	&__content {
		display: grid;
		grid-template-columns: 2fr 1.5fr;
		gap: map-get($space, l5);
		@include rwd-df(lg) {
			grid-template-columns: 1fr 1fr;
			gap: map-get($space, l3);
		}

		@include rwd-df(md) {
			grid-template-columns: 1fr;
			gap: map-get($space, l3);
		}

		&__text {
		}
		&__image {
			box-shadow: 0 10px 20px 0 map-get($color-grey, l7);
			border-radius: map-get($space, s5);
			@include rwd-df(md) {
				width: 65%;
				justify-self: center;
			}
			@include rwd-df(xs) {
				width: 85%;
			}

			@include rwd-only(xxs) {
				width: 100%;
			}
		}
	}
}
