/*
**  define Container component .
*/
.container {
	max-width: 1320px;
	padding-right: map-get($space, l1);
	padding-left: map-get($space, l1);
	margin-right: auto;
	margin-left: auto;

	@include rwd-df(xl) {
		max-width: 1140px;
	}

	@include rwd-df(lg) {
		max-width: 960px;
	}

	@include rwd-df(md) {
		max-width: 720px;
	}

	@include rwd-df(sm) {
		max-width: 540px;
	}

	@include rwd-df(xs) {
		width: 100%;
	}
}
