/*
**  define styling for element component with specified with classe  main header.
*/

.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;

	z-index: map-get($z-index, xxl2);

	&-contact {
		background-color: map-get($color-primary, main);
		color: $color-white;
		padding: map-get($space, s5) map-get($space, l1);

		display: flex;
		justify-content: space-between;
		align-items: center;
		@include rwd-df(md) {
			display: none;
		}

		&__link {
			&:link,
			&:visited {
				font-size: map-get($font-size, l1);
				color: $color-white;
				transition: all map-get($time, main);
			}
			&:hover,
			&:active {
				text-decoration: underline;
			}
		}
	}

	&-main {
		&.sticky {
			position: fixed;
			opacity: 0.95;
			top: 0;
			#{$start}: 0;
			#{$end}: 0;
			z-index: map-get($z-index, xxl2);
		}

		background-color: $color-white;
		position: relative;
		padding: map-get($space, s8) map-get($space, l1);
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-bottom: 2px solid map-get($color-grey, l6);
		.logo-link {
			&__image {
				height: 8rem;
				width: auto;
				width: 20rem;
				padding: 1rem 0;
			}
		}

		.navbar {
			flex-grow: 1;

			@include rwd-df(md) {
				visibility: hidden;
				// opacity: 0;
				pointer-events: none;
				transform-origin: top;
				transform: scaleY(0);
				transition: all map-get($time, l1) * 2;

				display: flex;
				flex-direction: column;

				background-color: $color-white;
				// background-color: rgba($color-white, 0.95);
				border-bottom: map-get($space, s7) solid map-get($color-grey, l6);
				border-bottom-right-radius: map-get($space, l1);
				border-bottom-left-radius: map-get($space, l1);
				overflow: hidden;

				position: absolute;
				top: 100%;
				#{$start}: 0;
				display: block;
				width: 100%;
				z-index: map-get($z-index, xxl1);
			}

			&__list {
				display: flex;
				justify-content: space-evenly;

				@include rwd-df(md) {
					display: flex;
					flex-direction: column;
					justify-content: center;
				}

				&__item {
					@include rwd-df(md) {
						&:not(:last-child) {
							border-bottom: map-get($space, s7) solid map-get($color-grey, l6);
						}
					}

					&__link {
						&:link,
						&:visited {
							font-weight: map-get($font-weight, l3);
							position: relative;
							display: inline-block;
							color: map-get($color-primary, l1);
							font-size: map-get($font-size, l2);
							transition: all map-get($time, l2);
							text-transform: uppercase;

							@include rwd-df(md) {
								display: block;

								text-align: center;
								padding: map-get($space, s3) 0;
							}

							&::after {
								content: '';
								display: block;
								width: 100%;
								height: 2px;
								background-color: map-get($color-primary, main);
								position: absolute;
								top: 110%;
								#{$start}: 0%;
								transform: scale(0);
								transition: all map-get($time, l2);
								@include rwd-df(md) {
									display: none;
								}
							}
						}
						&:hover,
						&:active {
							color: map-get($color-primary, main);
							@include rwd-df(md) {
								background-color: map-get($color-grey, l6);
							}

							&::after {
								transform: scale(100%);
							}
						}
					}
				}
			}
		}

		&.open .navbar {
			visibility: visible;
			opacity: 1;
			pointer-events: auto;
			transform: scaleY(1);
		}

		.toggler-btn {
			display: none;
			$bar-height: map-get($space, s6);
			$bar-transition: map-get($time, l1);
			$bar-color: $color-white;
			@include boxShadow(l2);
			transition: all map-get($time, main);

			&:hover {
				justify-content: space-around;
				transform: translateY(-0.1rem);
				box-shadow: 0 0.6rem 0.3rem rgb(0 0 0 / 40%);
			}

			&:focus {
				outline: none;
				transform: translateY(0);
				box-shadow: 0 0.5rem 0.3rem rgb(0 0 0 / 40%);
			}

			background-color: map-get($color-primary, main);
			border-radius: map-get($space, s4);

			padding: map-get($space, s2);
			@include rwd-df(md) {
				display: block;
			}

			&__bars {
				display: block;
				position: relative;
				@include squareSize(map-get($space, l2));
				&__bar {
					position: absolute;

					display: block;
					width: 100%;
					height: $bar-height;
					background-color: $bar-color;
					#{$start}: 0;
					border-radius: map-get($space, s2);

					transition: top $bar-transition $bar-transition,
						opacity $bar-transition $bar-transition, transform $bar-transition;

					&:nth-child(1) {
						top: 0;
					}
					&:nth-child(2) {
						top: calc(50% - ($bar-height / 2));
					}
					&:nth-child(3) {
						top: calc(100% - $bar-height);
					}
				}
			}
		}

		&.open .toggler-btn {
			$bar-height: map-get($space, s3);
			$bar-transition: map-get($time, l1);
			$bar-color: map-get($color-grey, main);

			&__bars {
				&__bar {
					transition: top $bar-transition, opacity $bar-transition,
						transform $bar-transition $bar-transition;

					&:nth-child(1) {
						top: calc(50% - ($bar-height / 2));
						transform: rotate(45deg);
					}
					&:nth-child(2) {
						opacity: 0;
					}
					&:nth-child(3) {
						top: calc(50% - ($bar-height / 2));
						transform: rotate(-45deg);
					}
				}
			}
		}
	}
}
