.dish-card {
	background-color: $color-white;

	border-radius: map-get($space, s3);
	overflow: hidden;
	display: flex;
	flex-direction: column;

	@include boxShadow(main);
	border: map-get($space, s7) solid map-get($color-primary, l1);

	&__image {
		// border-radius: map-get($space, s3);
	}
	&__text {
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		gap: map-get($space, main);
		padding: map-get($space, main);
	}
	&__title {
		font-family: inherit;
		font-size: 2.5rem;
		font-weight: bold;
		padding-bottom: map-get($space, main);
		color: rgb(25, 20, 20);
		border-bottom: 0.3rem solid map-get($color-primary, main);
		// margin-bottom: map-get($space, main);
	}
	&__paragraph {
	}

	&__actions {
		margin-top: auto;
		display: flex;
		justify-content: space-between;
		align-items: center;
		// margin-top: map-get($space, main);
		&__btn {
			&:link,
			&:visited {
				font-size: map-get($font-size, main);
				padding: map-get($space, s1) map-get($space, main);
			}

			@include rwd-df(xs) {
				&:link,
				&:visited {
					font-size: map-get($font-size, s1);
					padding: map-get($space, s2) map-get($space, s1);
				}
			}
		}
		&__price {
			font-size: map-get($font-size, l1);
			font-weight: map-get($font-weight, l3);
			color: map-get($color-primary, main);
		}
	}
}
