/*
**  define styling for element components with specified with classe main footer.
*/
.footer {
	position: relative;
	padding: map-get($space, s3) map-get($space, l1);
	background-color: map-get($color-primary, main);
	color: $color-white;
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: map-get($font-size, l2);
	font-weight: map-get($font-weight, l2);
	@include rwd-df(xs) {
		flex-direction: column;
		gap: 0.5rem;
	}

	&__copyright {
		font-size: map-get($font-size, main);
		@include rwd-only(xxs) {
			font-size: map-get($font-size, s1);
		}
	}

	&__lang-link {
		position: absolute;
		top: 50%;
		#{$end}: 50%;
		transform: translate(-50%, -50%);

		@include rwd-df(xs) {
			#{$end}: map-get($space, l1);
			transform: translateY(-50%);
		}

		&:link,
		&:visited {
			border-radius: map-get($space, s7);
			display: block;
			@include squareSize(map-get($space, l2));
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: $color-white;
			text-transform: uppercase;
			color: map-get($color-primary, main);
			transition: all map-get($time, main);
			border: map-get($space, s8) solid $color-white;
		}
		&:hover,
		&:active {
			background-color: transparent;
			border: map-get($space, s8) solid $color-white;
			color: $color-white;
			@include boxShadow(l2);
		}
	}
}
