.contact-form {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	@include rwd-df(xs) {
		grid-template-columns: 1fr;
	}
	gap: 3rem;

	.input-controll {
		&--textarea {
			grid-column: span 2;
			@include rwd-df(xs) {
				grid-column: span 1;
			}
		}

		&__input,
		&__textarea {
			display: block;
			width: 100%;
			font-size: 1.5rem;
			background-color: rgba(255, 255, 255, 0.5);
			padding: 1.5rem 2rem;
			border-radius: 0.2rem;
			border: 0.2rem solid rgb(151, 151, 151);
			transition: all 0.3s ease 0s;

			&:focus {
				box-shadow: rgba(0, 0, 0, 0.1) 0px 1rem 2rem;
				border-color: rgb(85, 197, 122);
				border-image: initial;
			}

			&:focus:invalid {
				border-color: rgb(255, 119, 48);
			}

			&:focus:invalid + label {
				display: block;
			}
		}

		&__label {
			display: none;
			color: red;
			font-size: 1.2rem;
			font-weight: 700;
			padding-left: 2rem;
			margin-top: 0.7rem;
		}

		&__textarea {
			resize: none;
		}
	}
}
